.card {
  display: flex;
  flex-direction: column;
  width: $card-width;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;

  @include hover(color, $card-title-hover-color, "product-name");
  @include hover(transform, scale(1.2));

  &__product-img {
    position: relative;
    padding: 0 0 80.7%;

    & > img {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      object-fit: contain;
    }
  }

  &__product-name {
    margin-top: 40px;

    @include fontStyle($font-weight: 500, $line-height: 150%);
  }

  &__product-code {
    margin-top: 4px;

    @include fontStyle(
      $font-size: 10px,
      $line-height: 140%,
      $color: $color-secondary
    );
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-top: 16px;
  }

  &__reviews {
    @include fontStyle($font-size: 10px, $line-height: 140%);
  }

  &__price {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
  }

  &__price-text {
    @include fontStyle($line-height: 150%, $color: $color-secondary);
  }

  &__price-value {
    @include fontStyle(16px, 700, 112%);
  }

  &__button {
    display: inline-flex;
    justify-content: center;
    align-items: center;
    text-transform: uppercase;

    @include fontStyle(14px, 700, 114%, #fff);

    background-color: $color-blue-accent;
    border-radius: 5px;
    padding: 11.5px 0;
    margin-top: 16px;
    user-select: none;
    border: 1px solid $color-blue-accent;

    @include hover(color, $color-blue-accent);
    @include hover(background-color, transparent);
  }
}
